import React from 'react';
import PropTypes from 'prop-types';
import PopoverMenu from '../../../common/PopoverMenu/PopoverMenu';
import { Field } from 'redux-form';

const InnerInstumentSelector = props => {
  const { bemClasses, instrumentsSymbols, selectedInstrument } = props;

  const options = props.options || [];

  return (
    <PopoverMenu
      customClass={bemClasses('instrument-selector')}
      popoverProps={{
        trigger: props => (
          <div className={bemClasses('instrument-container')} {...props}>
            <div className={bemClasses('symbols-wrapper')}>
              {instrumentsSymbols}
            </div>
            <div className={bemClasses('instruments-name')}>
              {selectedInstrument}
            </div>
            <div className={bemClasses('triangles-container')}>
              <span className={'triangle-up'} />
              <span className={'triangle-down'} />
            </div>
          </div>
        ),
        customClass: 'instrument-selector'
      }}
      items={options}
    />
  );
};

const RetailInstumentSelector = props => (
  <Field name={props.name} component={InnerInstumentSelector} {...props} />
);

RetailInstumentSelector.defaultProps = {
  name: ''
};

RetailInstumentSelector.propTypes = {
  name: PropTypes.string,
  instrumentsSymbols: PropTypes.string.isRequired,
  selectedInstrument: PropTypes.string.isRequired,
  bemClasses: PropTypes.func
};

export default RetailInstumentSelector;



