import React from 'react';
import withAuthentication from '../../hocs/withAuthentication';
import resize from '../../hocs/resize';
import RetailBuySellDesktopLayout from '../Responsive/RetailBuySellAltLayout/RetailBuySellDesktopLayout';
import RetailBuySellTabletsLayout from '../Responsive/RetailBuySellLayout/RetailBuySellTabletsLayout';
import RetailBuySellMobileLayout from '../Responsive/RetailBuySellAltLayout/RetailBuySellMobileLayout';
import SingleFluidHeaderFixedContentLayout from '../SingleFluidHeaderFixedContent/SingleFluidHeaderFixedContent';
import SingleFluidFixedContentLayout from '../SingleFluidFixedContent/SingleFluidFixedContentLayout';

class RetailBuySellLayout extends React.Component {
  renderContent = () => {
    const { width, footer } = this.props;

    if (width >= 768 && width <= 1280) {
      return (
        <SingleFluidFixedContentLayout footer={footer}>
          <RetailBuySellTabletsLayout />
        </SingleFluidFixedContentLayout>
      );
    }
    if (width < 768) {
      return (
        <SingleFluidHeaderFixedContentLayout footer={footer}>
          <RetailBuySellMobileLayout />
        </SingleFluidHeaderFixedContentLayout>
      );
    }
    return (
      <SingleFluidHeaderFixedContentLayout footer={footer}>
        <RetailBuySellDesktopLayout />
      </SingleFluidHeaderFixedContentLayout>
    );
  };
  render() {
    return this.renderContent();
  }
}

export default withAuthentication(resize(RetailBuySellLayout));



