import React from 'react';
import RetailMakeTransactionContainer from '../../../components/Retail/RetailBuySell/RetailMakeTransactionContainer';
import RetailOrderOverviewContainer from '../../../components/Retail/RetailBuySell/RetailOrderOverviewContainer';
import RetailSelectAnInstumentContainer from '../../../components/Retail/RetailBuySell/RetailSelectAnInstument/RetailSelectAnInstumentContainer';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import RetailBalancesContainer from '../../../components/Retail/RetailBuySell/RetailBalancesContainer';
import BuySellChartOverviewContainer from '../../../components/Retail/RetailBuySell/BuySellChartOverviewAlt/BuySellChartOverviewContainer';
import RecentActivityContainer from '../../../components/RecentActivity/RecentActivityContainer';

import './RetailBuySellDesktopLayout.css';

const classes = getBEMClasses('retail-buy-sell-layout');

const RetailBuySellDesktopLayout = () => {
  return (
    <div className={classes()}>
      <div className={classes('layout-header')} />
      <div className={classes('content-wrapper')}>
        <div className={classes('col', ['lg'])}>
          <RetailSelectAnInstumentContainer />
        </div>
        <div className={classes('col', ['sm', 'with-padding'])}>
          <RetailBalancesContainer customClass="compacted" />
        </div>
        <div className={classes('col', ['lg', 'with-bottom-padding'])}>
          <div
            className={`container-with-shadow ${classes(
              'chart-overview-container'
            )}`}>
            <BuySellChartOverviewContainer />
          </div>
        </div>
        <div className={classes('col', ['sm', 'with-padding'])}>
          <div
            className={classes(
              'make-transaction-container container-with-shadow',
              'alt-layout'
            )}>
            <RetailMakeTransactionContainer
              themeModificator="alt-layout"
              displayInstrumentSelector={false}
              extraInfoComponent={
                <RetailOrderOverviewContainer customClass="compacted" />
              }
            />
          </div>
        </div>
        <div className={classes('single-col')}>
          <RecentActivityContainer
            filterForSelected={false}
            useAdvancedVersion={false}
            headerText="Recent Orders"
          />
        </div>
      </div>
    </div>
  );
};

export default RetailBuySellDesktopLayout;



