import React from 'react';
import PropTypes from 'prop-types';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import { getBalanceWithProductSymbol } from '../../../helpers/productHelper';

import './RetailBalancesComponent.css';

const classes = getBEMClasses('retail-balances');

const RetailBalancesComponent = (props, context) => {
  const { product1, product2, customClass } = props;
  const product1Label = `${
    customClass === 'compacted' ? product1.ProductSymbol : ''
  } ${product1.ProductFullName}:`;
  const product2Label = `${
    customClass === 'compacted' ? product2.ProductSymbol : ''
  } ${product2.ProductFullName}:`;
  const product1Text = getBalanceWithProductSymbol(
    product1.ProductSymbol,
    product1.AvailableBalance,
    true
  );
  const product2Text = getBalanceWithProductSymbol(
    product2.ProductSymbol,
    product2.AvailableBalance,
    true
  );

  return (
    <div className={`${customClass} ${classes()}`}>
      <h3 className={`${customClass} ${classes('title')}`}>
        {context.t('Balances')}
      </h3>

      <div className={`${customClass} ${classes('content')}`}>
        <APLabelWithText
          customClass={`${customClass} ${classes()}`}
          label={product1Label}
          text={product1Text}
        />

        <APLabelWithText
          customClass={`${customClass} ${classes()}`}
          label={product2Label}
          text={product2Text}
        />
      </div>
    </div>
  );
};

RetailBalancesComponent.propTypes = {
  product1: PropTypes.object.isRequired,
  product2: PropTypes.object.isRequired
};

RetailBalancesComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailBalancesComponent;



