import { connect } from 'react-redux';
import BuySellChartIntervalSelectorComponent from './BuySellChartIntervalSelectorComponent';
import { changeTickersInterval } from '../../../redux/actions/tickersActions';

const mapStateToProps = state => {
  return {
    currentInterval: state.tickers.interval
  };
};

const mapDispatchToProps = {
  changeTickersInterval
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BuySellChartIntervalSelectorComponent);



