import React from 'react';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './SingleFluidFixedContentLayout.css';
import withAuthentication from '../../hocs/withAuthentication';

const layoutClasses = getBEMClasses('single-fluid-fixed-content-layout');

const SingleFluidFixedContentLayout = props => (
  <React.Fragment>
    <div className={layoutClasses('fixed-container')}>{props.children}</div>
    <div className={layoutClasses('footer')}>{props.footer}</div>
  </React.Fragment>
);

export default withAuthentication(SingleFluidFixedContentLayout);



