import React from 'react';
import RetailPriceChartContainer from '../../../RetailPriceChart/RetailPriceChartContainer';
import InstrumentTableContainer from '../../../InstrumentTable/InstrumentTableContainer';
import BuySellChartIntervalSelectorContainer from '../BuySellChartIntervalSelectorContainer';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import './BuySellChartOverviewComponent.css';
import INSTRUMENT_TABLE_ITEMS from '../../../InstrumentTable/InstrumentTableItems';

const chartOverviewClasses = getBEMClasses('chart-overview');

const BuySellChartOverviewComponent = props => {
  return (
    <div className={chartOverviewClasses('chart-container')}>
      <div className={chartOverviewClasses('title-container')}>
        <div className={chartOverviewClasses('title')}>Price Chart</div>
        <BuySellChartIntervalSelectorContainer />
      </div>
      <div className={chartOverviewClasses('instruments-bar-container')}>
        <InstrumentTableContainer
          customClass="chart-overview-alt-layout-top"
          items={[
            { field: INSTRUMENT_TABLE_ITEMS.lastPrice },
            { field: INSTRUMENT_TABLE_ITEMS.dailyPercentageChange },
            { field: INSTRUMENT_TABLE_ITEMS.dailyVolume },
          ]}
        />
      </div>
      <div className={chartOverviewClasses('chart-overview-container')}>
        <RetailPriceChartContainer customClass="chart-overview-alt-layout" />
      </div>
      <div className={chartOverviewClasses('instruments-bar-container')}>
        <InstrumentTableContainer
          customClass="chart-overview-alt-layout-bottom"
          items={[
            { field: INSTRUMENT_TABLE_ITEMS.bid },
            { field: INSTRUMENT_TABLE_ITEMS.ask },
            { field: INSTRUMENT_TABLE_ITEMS.dailyMax },
            { field: INSTRUMENT_TABLE_ITEMS.dailyMin },
          ]}
        />
      </div>
    </div>
  );
};

export default BuySellChartOverviewComponent;



