export const getBalanceWithProductSymbol = (
  productSymbol,
  productBalance,
  applyToUSDOnly = false
) => {
  if (productSymbol === 'USD') {
    return `$${productBalance}`;
  } else {
    if (applyToUSDOnly) {
      return productBalance;
    } else {
      return `${productSymbol} ${productBalance}`;
    }
  }
};



