import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import RetailInstumentSelector from './RetailInstumentSelector';
import './RetailSelectAnInstumentComponent.css';

const baseClasses = getBEMClasses('retail-select-an-instrument');

class RetailSelectAnInstumentComponent extends React.Component {
  componentWillMount() {
    const selectedInstrument = this.props.instruments.find(instrument => {
      return (
        instrument.InstrumentId === this.props.selectedInstrument.InstrumentId
      );
    });

    if (selectedInstrument) return;

    const instrumentId = this.props.instruments[0].InstrumentId;
    this.props.selectInstrument(instrumentId);
  }

  onSelect = instrumentId => {
    const { selectInstrument, clearForm } = this.props;

    selectInstrument(parseInt(instrumentId, 10));
    clearForm();
  };

  render() {
    const { instruments, selectedInstrument } = this.props;
    const { context } = this;

    return (
      <div className={baseClasses('container')}>
        <div className={baseClasses('label')}>
          {context.t('Select an Instrument:')}
        </div>
        <form>
          <RetailInstumentSelector
            name="instrument"
            options={instruments.map(instrument => ({
              label: `${instrument.Product1FullName} / ${
                instrument.Product2FullName
              }`,
              onClick: () => this.onSelect(instrument.InstrumentId)
            }))}
            instrumentsSymbols={selectedInstrument.Symbol}
            selectedInstrument={`${selectedInstrument.Product1FullName} / ${
              selectedInstrument.Product2FullName
            }`}
            bemClasses={baseClasses}
          />
        </form>
      </div>
    );
  }
}

RetailSelectAnInstumentComponent.propTypes = {
  instruments: PropTypes.array.isRequired,
  clearForm: PropTypes.func.isRequired,
  selectInstrument: PropTypes.func.isRequired,
  selectedInstrument: PropTypes.object.isRequired
};

RetailSelectAnInstumentComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailSelectAnInstumentComponent;



