import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../common/APButton';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import './BuySellChartIntervalSelectorComponent.css';

const classes = getBEMClasses('chart-interval-selector');

const BuySellChartIntervalSelectorComponent = props => {
  const timeIntervals = [
    {
      label: '1H',
      seconds: 3600
    },
    {
      label: '1D',
      seconds: 86400
    },
    {
      label: '1W',
      seconds: 604800
    },
    {
      label: '1M',
      seconds: 2678400
    },
    {
      label: '1Y',
      seconds: 31536000
    },
    {
      label: 'All',
      seconds: null
    }
  ];

  return (
    <div className={classes()}>
      {timeIntervals.map(item => (
        <div className={classes('item')} key={item.label}>
          <APButton
            baseClass={classes('item')}
            customClass={
              item.seconds === props.currentInterval ? 'selected' : null
            }
            onClick={() => props.changeTickersInterval(item.seconds)}>
            {item.label}
          </APButton>
        </div>
      ))}
    </div>
  );
};

BuySellChartIntervalSelectorComponent.propTypes = {
  currentInterval: PropTypes.number.isRequired,
  changeTickersInterval: PropTypes.func.isRequired
};

export default BuySellChartIntervalSelectorComponent;



